@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

:host {

  --col-blue: #E9F6FB;   /* New */
  --col-orange: #FFF1E3; /* In Progress */
  --col-green: #EAF7F0;  /* Done */
  --col-gray: #F2F4F7;   /* Stopped */
  --col-red: #FEECEC;    /* Late */
  --card-radius: 16px;
  --card-shadow: 0 4px 18px rgba(0,0,0,.05);
  display: block;
  font-family: 'IBM Plex Sans Arabic', system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans Arabic";
  overflow-x: hidden;
}

/* English font support */
:host-context([dir="ltr"]) {
  font-family: 'Poppins', system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

/* Page Header */
.page-title { 
  color: var(--brand-secondary); 
  font-size: 2.25rem; 
  font-weight: 700; 
  letter-spacing: .2px; 
  text-align: left;
}
.top-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}
.page-title[dir="rtl"] {
  text-align: right;
}

.page-title[dir="ltr"] {
  text-align: left;
}
.subtle { 
  color: #8294A8; 
  font-size: .95rem; 
}

/* Add Task Button */
.btn-add {
  background: #B68A35; 
  color: #fff; 
  border: none; 
  border-radius: 8px;
  font-weight: 600; 
  padding: 0.75rem 1.5rem; 
  transition: all 0.2s ease;
  font-size: 0.875rem;
}

.btn-add:hover {
  background: #B68A35;
}

.btn-add .plus { 
  display: inline-flex; 
  align-items: center; 
  justify-content: center; 
  width: 20px; 
  height: 20px; 
  border-radius: 4px; 
  background: #fff; 
  color: #B68A35; 
  margin-inline-end: 0.5rem; 
  font-weight: 700; 
  font-size: 0.875rem;
}

/* Kanban Board */
.kanban { 
  display: flex; 
  gap: 1rem; 
  overflow-x: auto; 
  padding-bottom: 1rem; 
  scroll-snap-type: x proximity; 
}

.kanban-col { 
  scroll-snap-align: start; 
  min-width: 200px; 
  max-width: 240px; 
  flex: 0 0 auto; 
}

.kanban-col.small { 
  min-width: 180px; 
}

/* Column Headers */
.col-head { 
  padding: 1rem 1.25rem; 
  border-radius: 12px; 
  font-weight: 600; 
  font-size: 0.95rem; 
  color: #fff; 
  margin-bottom: 1rem; 
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,.1);
}

.col-head.blue { 
  background: #6fb3d3a8; 
  color: #0091d4;
}

.col-head.orange { 
  background: #f7c9798f; 
  color: #B54708;
  /* border: 1px solid #B54708; */
}

.col-head.green { 
  background: #E5FAF2; 
  color: #00B789;
  /* border: 1px solid #00B789; */
}

.col-head.gray { 
  color: #404d49b9;
  background: #4e5c703d; 
}

.col-head.red { 
  color: #d80f0f;
  background: #f7151534; 
}

.col-head.purple { 
  color: #34495E;
  background: #34495e1f; 
}

.col-head.yellow { 
  color: #eab308;
  background: #fefce8; 
}

/* Task Count Detail */
.task-count-detail {
  margin-top: 0.25rem;
  font-size: 0.75rem;
  opacity: 0.8;
}

.task-count-detail small {
  display: block;
  line-height: 1.2;
}

/* Task Lists */
.task-list { 
  display: flex; 
  flex-direction: column; 
  gap: .8rem; 
}

/* Task Cards */
.task-card {
  position: relative; 
  background: #fff; 
  border: 1px solid rgba(9,54,95,.08); 
  border-radius: var(--card-radius);
  box-shadow: var(--card-shadow); 
  padding: .6rem .75rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.task-card.compact { 
  padding: .5rem .65rem; 
}

.task-card:hover { 
  border-color: rgba(9,54,95,.18); 
  box-shadow: 0 8px 25px rgba(0,0,0,.08);
}

/* Task Content */
.task-content {
  margin-top: 1rem;
}

.task-title { 
  font-weight: 600; 
  color: #173653; 
  font-size: 0.95rem; 
  margin: 0 0 .2rem; 
  line-height: 1.2;
}

.task-desc { 
  color: #7B8DA1; 
  font-size: 0.85rem; 
  margin: 0 0 .3rem; 
  line-height: 1.3;
  display: -webkit-box; 
  -webkit-line-clamp: 3; 
  line-clamp: 3;
  -webkit-box-orient: vertical; 
  overflow: hidden; 
}

.task-meta { 
  font-size: .82rem; 
  color: #95A6B8; 
}

.task-dates {
  margin-top: 0.5rem;
}

.task-dates small {
  font-size: 0.75rem;
  line-height: 1.2;
}

.task-footer { 
  display: flex; 
  align-items: center; 
  justify-content: space-between; 
  gap: .5rem; 
  margin-top: .35rem; 
}

/* Status-based borders */
.task-card.status-new:hover { border: 1px solid #0ea5e9; }
.task-card.status-progress:hover { border:1px solid #fb923c; }
.task-card.status-done:hover { border:1px solid #22c55e; }
.task-card.status-stopped:hover { border:1px solid #94a3b8; }
.task-card.status-late:hover { border:1px solid #ef4444; }
.task-card.status-returned:hover { border:1px solid 34495E; }
.task-card.status-review:hover { border:1px solid #eab308; }

/* Status badges */
.badge.status-new {
  background-color: #0ea5e9;
  color: white;
}

.badge.status-inProgress {
  background-color: #fb923c;
  color: white;
}

.badge.status-done {
  background-color: #22c55e;
  color: white;
}

.badge.status-stopped {
  background-color: #94a3b8;
  color: white;
}

.badge.status-late {
  background-color: #ef4444;
  color: white;
}

.badge.status-returned {
  background-color: #34495E;
  color: white;
}

.badge.status-review {
  background-color: #eab308;
  color: white;
}

/* Priority badges */
.badge {
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  font-weight: 600;
  border-radius: 6px;
}

.priority-high {
  background: #FEF2F2;
  color: #DC2626;
}

.priority-medium {
  background: #FFFBEB;
  color: #D97706;
}

.priority-low {
  background: #F0FDF4;
  color: #16A34A;
}

/* Status badges */
.badge-status {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  font-weight: 600;
  border-radius: 8px;
}

.badge-status.new { background: #E9F6FB; color: #0ea5e9; }
.badge-status.progress { background: #FFF1E3; color: #d97706; }
.badge-status.done { background: #EAF7F0; color: #16a34a; }
.badge-status.stopped { background: #F2F4F7; color: #475569; }
.badge-status.late { background: #FEECEC; color: #b91c1c; }
.badge-status.returned { background: #34495e3f; color: #34495E; }
.badge-status.review { background: #fefce8; color: #eab308; }

/* Bulk Selection */
.bulk-check { 
  position: absolute; 
  inset-inline-end: .55rem; 
  inset-block-start: .55rem; 
  transform: scale(1.1); 
  z-index: 10;
}

/* Options Menu */
.dropdown-menu {
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.05);
}

.dropdown-item {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  transition: all 0.2s ease;
}

.dropdown-item:hover {
  background: #F8FAFC;
}

.dropdown-item.text-danger:hover {
  background: #FEF2F2;
}

/* Bulk Toolbar */
.bulk-bar { 
  position: sticky; 
  bottom: 0; 
  inset-inline: 0; 
  background: #fff; 
  border-top: 1px solid #E6EEF5;
  padding: .6rem .85rem; 
  box-shadow: 0 -6px 18px rgba(0,0,0,.04); 
  display: none; 
  z-index: 1030; 
}

.bulk-bar.show { 
  display: block; 
}

/* Modal Styling */
.modal {
  z-index: 1050;
}

.modal.fade.show {
  display: block !important;
  background: rgba(0, 0, 0, 0.5);
}

.modal-backdrop {
  background: rgba(0, 0, 0, 0.5);
  z-index: 1040;
}

.modal-content {
  border-radius: 16px;
  border: none;
  box-shadow: 0 20px 25px -5px rgba(0,0,0,.1), 0 10px 10px -5px rgba(0,0,0,.04);
}

.modal-header {
  border-bottom: 1px solid #E6D7A2;
  padding: 1.5rem 1.5rem 1rem;
  background: #D4AF5F;;
  color: #5F646D;
  display: flex;
  align-items: center;
  position: relative;
  min-height: 40px;
}

.modal-body {
  padding: 1.5rem;
}

.modal-footer {
  border-top: 1px solid #E6D7A2;
  padding: 1rem 1.5rem 1.5rem;
}

.modal-title {
  font-weight: 700;
  color: #5F646D;
  flex: 1;
  width: 100%;
  margin: 0;
}

/* LTR Layout: Title on left */
[dir="ltr"] .modal-title {
  text-align: left !important;
}

/* RTL Layout: Title on right */
[dir="rtl"] .modal-title {
  text-align: right !important;
}

/* Close Button styles removed - using cancel buttons in footer instead */

/* Form Controls */
.form-label {
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.5rem;
}

.form-control, .form-select {
  border-radius: 8px;
  border: 1px solid #D1D5DB;
  padding: 0.75rem;
  transition: all 0.2s ease;
}

.form-control:focus, .form-select:focus {
  border-color: #B68A35;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

/* Buttons */
.btn {
  border-radius: 8px;
  font-weight: 600;
  padding: 0.75rem 1.5rem;
  transition: all 0.2s ease;
}

.btn-primary {
  background: #B68A35;
  border-color: #B68A35;
  color: #FFFFFF;
}

.btn-primary:hover {
  background: #A87D2A;
  border-color: #A87D2A;
  color: #FFFFFF;
}
.edit-task-btn {
  color: #29ABE2;
}
.edit-task-btn:hover {
  background: #29ABE2;
  color: #FFFFFF;
}
.btn-outline-primary {
  color: #B68A35;
  border-color: #B68A35;
  background: transparent;
}

.btn-outline-primary:hover {
  background: #B68A35;
  border-color: #B68A35;
  color: #FFFFFF;
}

.btn-outline-warning {
  color: #f59e0b;
  border-color: #f59e0b;
  background: transparent;
}

.btn-outline-warning:hover {
  background: #f59e0b;
  border-color: #f59e0b;
  color: white;
}

.btn-outline-info {
  color: #3b82f6;
  border-color: #3b82f6;
  background: transparent;
}

.btn-outline-info:hover {
  background: #3b82f6;
  border-color: #3b82f6;
  color: white;
}

.btn-outline-secondary {
  color: #1B1D21 !important;
  border-color: #1B1D21 !important;
  background: transparent !important;
}
.btn-reassign{
  color: #555555;
  border-color: #555555;
  background: transparent;
}
.btn-reassign:hover {
  background: #555555;
  color: #FFFFFF;
}
.btn-outline-secondary:hover {
  background: #1B1D21;
  color: #FFFFFF;
}

.btn-outline-danger {
  color: #C62828;
  border-color: #C62828;
  background: transparent;
}

.btn-outline-danger:hover {
  background: #C62828;
  border-color: #C62828;
  color: #FFFFFF;
}

/* Cancel Button Styles */
.btn-cancel, .btn-secondary {
  background: transparent !important;
  border-color: #1B1D21;
  color: #1B1D21;
}

.btn-cancel:hover, .btn-secondary:hover {
  background: #1B1D21 !important;
  color: #FFFFFF;
}

/* File Upload Area */
.border.rounded {
  border: 2px dashed #D1D5DB !important;
  transition: all 0.2s ease;
}

.border.rounded:hover {
  border-color: var(--brand-primary) !important;
  background: #F8FAFC;
}

.cursor-pointer {
  cursor: pointer;
}

/* Loading Spinner */
.spinner-border {
  width: 3rem;
  height: 3rem;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .kanban-col { 
    min-width: 220px; 
    max-width: 260px;
  }
}

@media (max-width: 992px) {
  .kanban-col { 
    min-width: 200px; 
    max-width: 240px;
  }
  
  .kanban {
    gap: 0.75rem;
  }
}

@media (max-width: 768px) {
  .kanban { 
    flex-direction: column;
    gap: 1rem;
  }
  
  .kanban-col { 
    min-width: 100%; 
    max-width: 100%;
  }
  
  .col-head {
    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }
  
  .task-card {
    padding: 0.5rem 0.6rem;
  }
  
  .task-title {
    font-size: 0.9rem;
  }
  
  .task-desc {
    font-size: 0.8rem;
  }
  
  .modal-dialog {
    margin: 0.5rem;
    max-width: calc(100vw - 1rem);
  }
  
  .bulk-bar {
    padding: 0.5rem;
  }
  
  .bulk-bar .d-flex {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .btn-add {
    padding: 0.6rem 1.2rem;
    font-size: 0.8rem;
  }
  
  .btn-add .plus {
    width: 18px;
    height: 18px;
    font-size: 0.8rem;
  }
}

@media (max-width: 576px) {
  .container-fluid {
    padding-left: 0.75rem;
    padding-right: 0.75rem;
  }
  
  .page-title {
    font-size: 1.5rem;
  }
  
  .subtle {
    font-size: 0.85rem;
  }
  
  .task-card.compact {
    padding: 0.5rem 0.6rem;
  }
  
  .task-content {
    margin-top: 1rem;
  }
  
  .modal-body {
    padding: 1rem;
  }
  
  .modal-header {
    padding: 1rem 1rem 0.75rem;
  }
  
  .modal-footer {
    padding: 0.75rem 1rem 1rem;
  }
}

/* RTL Support */
[dir="rtl"] .bulk-check {
  inset-inline-end: auto;
  inset-inline-start: .55rem;
}

[dir="rtl"] .dropdown {
  right: auto;
  left: 0.5rem;
}

[dir="rtl"] .task-footer {
  flex-direction: row-reverse;
}

/* Animation for modals */
.modal.fade.show {
  animation: modalFadeIn 0.3s ease-out;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Task card hover effects */
.task-card {
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.task-card:hover {
  box-shadow: 0 8px 25px rgba(0,0,0,.08);
}

/* Status badges in task details */
.badge-status { 
  font-weight: 600; 
  padding: 0.5rem 1rem;
  border-radius: 8px;
}

.badge-status.new { background: #E9F6FB; color: #0ea5e9; }
.badge-status.progress { background: #FFF1E3; color: #d97706; }
.badge-status.done { background: #EAF7F0; color: #16a34a; }
.badge-status.stopped { background: #F2F4F7; color: #475569; }
.badge-status.late { background: #FEECEC; color: #b91c1c; }
.badge-status.returned { background: #f3e8ff; color: 34495E; }
.badge-status.review { background: #fefce8; color: #eab308; }

/* Scrollbar styling for kanban */
.kanban::-webkit-scrollbar {
  height: 8px;
}

.kanban::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.kanban::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.kanban::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Task Count in Column Headers */
.task-count {
  font-size: 0.8rem;
  font-weight: 500;
  opacity: 0.8;
  margin-left: 0.5rem;
}

/* Pagination Container */
.pagination-container {
  margin-top: 2rem;
  padding: 1.5rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #e9ecef;
}

/* Responsive pagination layout */
@media (max-width: 768px) {
  .pagination-container {
    padding: 1rem;
  }
  
  .pagination {
    gap: 0.125rem;
  }
  
  .pagination .page-link {
    min-width: 36px;
    height: 36px;
    padding: 0.375rem 0.5rem;
    font-size: 0.8rem;
  }
  
  .pagination-info {
    font-size: 0.8rem;
    margin-bottom: 1rem;
  }
}

/* Pagination Styles */
.pagination {
  margin-bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  flex-wrap: wrap;
}

.pagination .page-item {
  margin: 0;
}

.pagination .page-link {
  color: #B68A35;
  border: 1px solid #e9ecef;
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
  height: 40px;
  font-weight: 500;
  font-size: 0.875rem;
}

.pagination .page-link:hover:not(.disabled) {
  background-color: #B68A35;
  color: white;
  border-color: #B68A35;
  box-shadow: 0 4px 8px rgba(38, 102, 127, 0.2);
}

.pagination .page-item.active .page-link {
  background-color: #B68A35;
  border-color: #B68A35;
  color: white;
  box-shadow: 0 2px 4px rgba(38, 102, 127, 0.3);
  font-weight: 600;
}

.pagination .page-item.disabled .page-link {
  color: #adb5bd;
  background-color: #f8f9fa;
  border-color: #e9ecef;
  cursor: not-allowed;
  opacity: 0.5;
}

.pagination .page-item.disabled .page-link:hover {
  background-color: #f8f9fa;
  color: #adb5bd;
  border-color: #e9ecef;
  transform: none;
  box-shadow: none;
}

/* Arrow icon styling */
.arrow-icon {
  display: inline-block;
  transition: transform 0.2s ease;
}

.arrow-icon.rtl-arrow {
  /* RTL arrows are flipped for proper direction */
  transform: scaleX(-1);
}

/* Navigation buttons styling */
.pagination .page-link i {
  font-size: 0.875rem;
}

/* Pagination Info */
.pagination-info {
  text-align: center;
  font-size: 0.875rem;
  color: #6c757d;
  font-weight: 500;
}

/* RTL Support for Pagination */
[dir="rtl"] .pagination {
  direction: rtl;
}

[dir="rtl"] .pagination-info {
  text-align: center;
}

/* Modal Z-index Hierarchy */
.modal {
  z-index: 9999 !important;
}

.modal.show {
  z-index: 9999 !important;
}

.modal-backdrop {
  z-index: 9998 !important;
}

/* Task Details Modal - Lower z-index */
.modal-xl {
  z-index: 9999 !important;
}

/* Edit/Status/Other Modals - Higher z-index */
.modal-lg {
  z-index: 9999 !important;
}

.modal-dialog:not(.modal-xl) {
  z-index: 9999 !important;
}

/* Confirmation Modal - Highest z-index */
app-confirmation-modal .modal-overlay {
  z-index: 99999;
}

/* Ensure modals are properly layered */
.modal.fade.show {
  z-index: inherit;
}

/* Prevent sidebar from appearing above modals */
.sidebar,
.sidebar * {
  z-index: 1000 !important;
}

/* Ensure modals are above everything */
.modal,
.modal * {
  z-index: 9999 !important;
}

/* Background Blur when Modals are Open */
body.modal-open {
  overflow: hidden;
}

body.modal-open::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 1039;
  pointer-events: none;
}

/* Ensure modals have proper backdrop */
.modal-backdrop {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  background: rgba(0, 0, 0, 0.6) !important;
}

/* Additional modal backdrop blur */
.modal.show {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

/* KPI Cards */
.kpi-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  position: relative;
  transition: all 0.3s ease;
  height: 120px;
  min-width: 280px;
  max-width: 320px;
  margin-bottom: 1rem;
  border: 1px solid rgba(238, 238, 238, 0.05);
}

/* Font support for KPI cards */
.kpi-card[dir="ltr"] {
  font-family: 'Poppins', system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

.kpi-card[dir="rtl"] {
  font-family: 'IBM Plex Sans Arabic', system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans Arabic";
}

/* KPI Cards Row Spacing */
.kpi-cards-row {
  margin-bottom: 2rem;
}

/* Bootstrap grid gap support */
.kpi-cards-row.g-4 {
  --bs-gutter-x: 19.7rem;
  --bs-gutter-y: 1.5rem;
}

.kpi-cards-row.g-4 > * {
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-top: calc(var(--bs-gutter-y) * 0.5);
  margin-bottom: calc(var(--bs-gutter-y) * 0.5);
}

/* Ensure proper card spacing */
.kpi-cards-row .col-lg-2,
.kpi-cards-row .col-md-4,
.kpi-cards-row .col-sm-6 {
  margin-bottom: 1.5rem;
}

/* Card hover effects with proper spacing */
.kpi-cards-row .kpi-card {
  margin-bottom: 0;
  height: 100%;
}

/* Ensure consistent card heights */
.kpi-cards-row .kpi-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Add some breathing room between card rows */
.kpi-cards-row + .row {
  margin-top: 2rem;
}

/* Responsive card adjustments */
@media (max-width: 1200px) {
  .kpi-cards-row .col-lg-2 {
    margin-bottom: 1rem;
  }
}

@media (max-width: 768px) {
  .kpi-cards-row .col-md-4 {
    margin-bottom: 1rem;
  }
}

@media (max-width: 576px) {
  .kpi-cards-row .col-sm-6 {
    margin-bottom: 1rem;
  }
}

.kpi-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

/* Hover border colors matching icon colors - full card border */
.kpi-total:hover {
  border: 1px solid #09365F; /* Dark blue matching icon text */
}

.kpi-progress:hover {
  border: 1px solid #B54708; /* Dark orange matching icon text */
}

.kpi-completed:hover {
  border: 1px solid #0f7a2f; /* Dark green matching icon text */
}

.kpi-late:hover {
  border: 1px solid #dc2626; /* Dark red matching icon text */
}

.kpi-pending:hover {
  border: 1px solid #6b7280; /* Dark gray matching icon text */
}

.kpi-completion-rate:hover {
  border: 1px solid #059669; /* Dark green matching icon text */
}

.kpi-content {
  display: flex;
  align-items: center;
  height: 100%;
}

.kpi-icon {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  flex-shrink: 0;
}

.kpi-total .kpi-icon {
  background-color: rgb(204, 227, 237); /* أزرق سماوي فاتح */
  color: #09365F; /* Dark blue text for contrast */
}

.kpi-progress .kpi-icon {
  background-color: rgb(251, 236, 212); /* أصفر/برتقالي فاتح (بيج) */
  color: #B54708; /* Dark orange text for contrast */
}

.kpi-completed .kpi-icon {
  background-color: rgb(237, 250, 246); /* أخضر فاتح مائل للتركواز */
  color: #0f7a2f; /* Dark green text for contrast */
}

.kpi-late .kpi-icon {
  background-color: rgb(253, 226, 226); /* وردي فاتح */
  color: #dc2626; /* Dark red text for contrast */
}

.kpi-pending .kpi-icon {
  background-color: rgb(248, 250, 252); /* رمادي فاتح محايد */
  color: #6b7280; /* Dark gray text for contrast */
}

.kpi-completion-rate .kpi-icon {
  background-color: rgb(237, 250, 246); /* أخضر فاتح مائل للتركواز */
  color: #059669; /* Dark green text for contrast */
}

.kpi-info {
  flex: 1;
}

.kpi-number {
  font-size: 2rem;
  font-weight: 700;
  color: #1f2937;
  line-height: 1;
  margin-bottom: 0.25rem;
}

.kpi-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
  text-transform: capitalize;
}

.kpi-subtitle {
  font-size: 0.75rem;
  color: #9ca3af;
  font-weight: 400;
  margin-top: 0.25rem;
  font-style: italic;
}

.completion-progress {
  margin-top: 0.75rem;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background-color: #e5e7eb;
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 0.5rem;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #158638 0%, #059669 100%);
  border-radius: 3px;
  transition: width 0.8s ease-in-out;
}

.progress-labels {
  display: flex;
  justify-content: space-between;
  font-size: 0.625rem;
  color: #9ca3af;
  font-weight: 500;
}

/* Insight Cards */
.insight-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  height: 100%;
  border-left: 4px solid #B68A35;
}

.insight-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.insight-title {
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}

.insight-icon {
  width: 32px;
  height: 32px;
  background-color: #f3f4f6;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6b7280;
}

.insight-content {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.insight-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid #f3f4f6;
}

.insight-item:last-child {
  border-bottom: none;
}

.insight-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  color: #374151;
  font-weight: 500;
}

.insight-value {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.insight-count {
  font-weight: 600;
  color: #1f2937;
}

.insight-percentage {
  color: #6b7280;
  font-size: 0.75rem;
}

.status-dot, .priority-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

