/* Efficiency Summary Card */
.efficiency-summary-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16px;
  padding: 2rem;
  color: white;
  box-shadow: 0 8px 32px rgba(102, 126, 234, 0.3);
}

.summary-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}

.summary-title-section {
  flex: 1;
}

.summary-title {
  font-size: 1.75rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
  color: white;
}

.summary-subtitle {
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
  font-weight: 400;
}

.summary-metrics {
  display: flex;
  gap: 2rem;
  flex-shrink: 0;
}

.metric-item {
  text-align: center;
  min-width: 80px;
}

.metric-value {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.metric-label {
  font-size: 0.75rem;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.summary-chart {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.chart-container {
  display: flex;
  align-items: end;
  gap: 1rem;
  height: 120px;
  width: 100%;
  justify-content: center;
}

.chart-bar {
  width: 40px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 4px 4px 0 0;
  position: relative;
  transition: all 0.6s ease-out;
  animation: chartBarGrow 0.8s ease-out forwards;
  transform: scaleY(0);
  transform-origin: bottom;
}

.chart-bar:hover .chart-tooltip {
  opacity: 1;
  transform: translateY(-5px);
}

.chart-tooltip {
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 0.5rem;
  border-radius: 6px;
  font-size: 0.75rem;
  white-space: nowrap;
  opacity: 0;
  transition: all 0.2s ease;
  pointer-events: none;
}

.chart-tooltip::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 4px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.8);
}

.chart-labels {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.chart-label {
  font-size: 0.75rem;
  opacity: 0.8;
  text-align: center;
  min-width: 40px;
}

@keyframes chartBarGrow {
  from {
    transform: scaleY(0);
  }
  to {
    transform: scaleY(1);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .kpi-card {
    min-width: 100%;
    height: auto;
    min-height: 120px;
  }
  
  .summary-header {
    flex-direction: column;
    gap: 1rem;
  }
  
  .summary-metrics {
    gap: 1rem;
    justify-content: space-around;
    width: 100%;
  }
  
  .metric-item {
    min-width: auto;
    flex: 1;
  }
  
  .chart-container {
    gap: 0.5rem;
  }
  
  .chart-bar {
    width: 30px;
  }
  
  .chart-labels {
    gap: 0.5rem;
  }
  
  .chart-label {
    font-size: 0.625rem;
    min-width: 30px;
  }
  
  .insight-card {
    margin-bottom: 1rem;
  }
  
  .insight-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .insight-icon {
    align-self: flex-end;
  }
}

/* RTL Support for KPI Cards */
[dir="rtl"] .kpi-content {
  flex-direction: row-reverse;
}

[dir="rtl"] .kpi-icon {
  margin-right: 0;
  margin-left: 1rem;
}

[dir="rtl"] .kpi-info {
  text-align: right;
}

/* RTL Support for Modal Headers - Removed old conflicting rules */

/* RTL Dropdown Arrow Alignment */
[dir="rtl"] .form-select {
  background-position: left 0.75rem center;
  padding-left: 2.25rem;
  padding-right: 0.75rem;
}

[dir="ltr"] .form-select {
  background-position: right 0.75rem center;
  padding-right: 2.25rem;
  padding-left: 0.75rem;
}

/* Ensure dropdown arrows don't overlap with text */
[dir="rtl"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

[dir="ltr"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

/* Focus state for RTL dropdowns */
[dir="rtl"] .form-select:focus {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select:focus {
  background-position: right 0.75rem center;
}

/* Invalid state for RTL dropdowns */
[dir="rtl"] .form-select.is-invalid {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select.is-invalid {
  background-position: right 0.75rem center;
}
